  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Create a Date object using the provided date string
    const postDate = new Date(dateString);

    // Create an instance of Intl.DateTimeFormat for the "Europe/Oslo" timezone
    const options = {
      timeZone: "Europe/Oslo",
      year: "numeric",
      month: "numeric",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false,
    };

    // Get the date and time in the specified timezone
    const localDate = new Intl.DateTimeFormat("en-US", options).format(
      postDate
    );

    // Get the current date in the same timezone
    const now = new Date();
    const localNow = new Intl.DateTimeFormat("en-US", options).format(now);

    // Parse the formatted dates back into Date objects
    const postDateLocal = new Date(localDate);
    const nowLocal = new Date(localNow);

    // Calculate the difference in seconds
    const secondsDiff = Math.floor((nowLocal - postDateLocal) / 1000);

    // Define time intervals
    const intervals = [
      { label: "year", seconds: 31536000 }, // 365 days
      { label: "month", seconds: 2592000 }, // 30 days
      { label: "day", seconds: 86400 }, // 1 day
      { label: "hour", seconds: 3600 }, // 1 hour
      { label: "minute", seconds: 60 }, // 1 minute
      { label: "second", seconds: 1 }, // 1 second
    ];

    for (const interval of intervals) {
      const count = Math.floor(secondsDiff / interval.seconds);
      if (count >= 1) {
        return `${count} ${interval.label}${count > 1 ? "s" : ""} ago`;
      }
    }

    return "Just now";
  };
  const formateTimeDiff = (dateString) => {
    if (!dateString) return null;

    // Parse the date as UTC
    const postDate = new Date(dateString);
    if (isNaN(postDate.getTime())) {
      console.error("Invalid date:", dateString);
      return "Invalid date";
    }

    // Get the current time in UTC
    const now = new Date();

    // Adjust the postDate by subtracting 1 hour (3600000ms)
    const adjustedPostDate = new Date(postDate.getTime() - 3600000);

    // Convert postDate to Europe/Oslo time correctly
    const osloPostTime = new Intl.DateTimeFormat("en-GB", {
      timeZone: "Europe/Oslo",
      hour: "2-digit",
      minute: "2-digit",
      hour12: false, // Ensures 24-hour format
    }).format(adjustedPostDate);

    // Calculate time difference in seconds, minutes, hours, days
    const timeDiff = now.getTime() - postDate.getTime();
    const seconds = Math.floor(timeDiff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);
    const weeks = Math.floor(days / 7);
    const months = Math.floor(days / 30);
    const years = Math.floor(days / 365);

    // Generate formatted output based on the difference
    if (minutes < 60) return `${osloPostTime}`;
    if (hours < 24) return `${osloPostTime}`;
    if (days === 1) return `Yesterday at ${osloPostTime}`;
    if (days < 7) return `${days}d  ${osloPostTime}`;
    if (weeks < 4) return `${weeks}w  ${osloPostTime}`;
    if (months < 12) return `${months}m  ${osloPostTime}`;
    return `${years}y  ${osloPostTime}`;
  };
